<template>
<div class="header">
    <div class="header-left"
        v-if="hasHeaderLeft"
        @click="back">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#h-icon-back"></use>
      </svg>
    </div>
    <div class="title">
        {{title}}
    </div>
    <div class="header-right">

    </div>
</div>
</template>
<script>
export default {
  name: "Header",
  props: {
    hasHeaderLeft:{
      type:Boolean,
      default:true
    },
    title: {
      type: String,
      required: true
    }
  },
  methods:{
    back(){
      this.$router.go(-1)
    }
  }
};
</script>
<style scoped lang='scss'>
</style>


